<template>
  <div>
    <van-nav-bar title="商品列表" left-arrow @click-left="$router.go(-1)" />
    <div>
      <van-card
        tag="hot"
        :price="item.price"
        :title="item.goodsname"
        :thumb="$preImg + item.img"
        v-for="item in goodsList"
        :key="item.id"
      >
        <template #footer>
          <van-button size="mini" @click="add(item.id)">加入购物车</van-button>
        </template>
      </van-card>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import { getGoodsList,addCart } from "../utils/request";
export default {
  data() {
    return {
      goodsList: [],
    };
  },
  methods: {
    requestGoodsList() {
      const id = this.$route.query.fid;
      getGoodsList({ id }).then((res) => {
        this.goodsList = res.data.list;
      });
    },
    //添加购物车
    add(goodsid) {
      //接口需要的参数
      var data = {
        uid: this.user.uid,
        goodsid,
        num: 1,
      };
      //添加购物车请求
      addCart(data).then((res) => {
        this.$toast.success("添加购物车成功");
        this.requestCartList(this.user.uid);
      });
    },
  },
  computed:{
    ...mapGetters({
      "user":"user",
    })
  },
  mounted() {
    this.requestGoodsList();
  },
};
</script>

<style scoped>
.van-button {
  background-color: rgb(107, 207, 112);
  color: aliceblue;
  border-radius: 5px;
}
</style>


